export default function BlogCard() {
  return (
    <Fragment>
      <div className="flex flex-row flex-wrap gap-2 px-[5%] justify-between">
        {cardData.map((item, index) => {
          return (
            <Fragment key={index}>
              <div
                key={index}
                className="w-[20%] max-lg:w-[45%] max-md:w-full  group  flex flex-col items-center justify-between pb-4  cursor-pointer rounded-[20px] overflow-hidden border border-[#08090c]  transition-colors duration-300 ease-in-out hover:border-[#0a49eb] text-white  "
              >
                <Avatar className=" w-full h-auto transition-transform rounded-none duration-300 ease-in-out group-hover:scale-105">
                  <AvatarImage
                    src={item.image}
                    alt="Service Card 1"
                    className="w-full lg:h-[200px] object-cover rounded-none  "
                  />
                  <AvatarFallback>Card {index}</AvatarFallback>
                </Avatar>
                <div className="p-6 space-y-2 border border-transparent border-b-[#08090c]">
                  <h4 className="lg:text-[16px]   font-[600]">{item.title}</h4>
                  <p className="lg:text-[14px]  font-[400] ">{item.text}</p>
                </div>
                {item.button}
              <div className="flex flex-row gap-x-2 justify-center  items-center">
                  <Avatar  >
                    <AvatarImage
                      src={item.icon}
                      className="w-25 h-10 grayscale"
                    />
                    <AvatarFallback>Team Member</AvatarFallback>
                  </Avatar>
                <div className="">
                    <h4>{item.name}</h4>
                    <h4>{item.date}</h4>
                </div>
              </div>
              </div>
            </Fragment>
          );
        })}
      </div>
    </Fragment>
  );
}
